import React from 'react';

const pb1 = () => (
  <section className='withLeftHeader doubledHeader'>
    <h1>Politechnika Biaostocka</h1>
    <article className='descSections verticalAligned'>
      <div>
        <div>kierunek</div>
        <b>Informatyka II stopnia</b>
      </div>
      <div>
        <div>Specjalno</div>
        <div>Grafika i multimedia</div>
      </div>
      <div>
        <div>uzyskany tytu</div>
        <div>Magister inynier</div>
      </div>
      <div>
        <div>tytu pracy magisterskiej</div>
        <div>"Porwnanie wspczesnych standardw prezentacji grafiki 3D w przegldarkach internetowych."</div>
      </div>
      <div>
        <div>czas trwania</div>
        <div>padziernik 2011 - czerwiec 2013</div>
      </div>
    </article>
  </section>
)

export default pb1;



