import React from 'react';
import impro1 from '../../images/impro1.jpg';
import impro2 from '../../images/impro2.jpg';
import impro3 from '../../images/impro3.jpg';

const impro = () => (
  <section className='withLeftHeader doubledHeader'>
    <h1>improwizacj sceniczn</h1>
    <article>
      <div>
        <div>
          Na tzw. "impro", czyli spektakle bez scenariusza powstajce na oczach widzw trafiem przypadkiem na pocztku 2016 roku. Od tego czasu aktywnie uczestnicz w tego typu wydarzeniach zarwno jako widz jak i uczestnik.
        </div>
        <div className='imagesWeb'>
          <img src={impro1} alt='impro 1'/>
          <img src={impro2} alt='impro 2'/>
        </div>
        <div>
          Uczszczaem na kursy do Szkoy Impro, Impro.labu, do szkoy "S". Obecnie nale do jednej z warszawskich grup improwizacyjnych z ktr wystpuj raz w miesicu. Niekiedy  bior te udzia w "demach impro".
        </div>
        <div className='imagesWeb'>
          <img src={impro3} alt='impro 3'/>
        </div>
      </div>
    </article>
  </section>
)

export default impro;



