import React from 'react';
import testWebGL from '../../../images/testWebGL.png';

const IMG_TITLE = 'Screen z testu wydajnoci grafiki 3D w przegldarkach';

const pb2 = () => (
  <section className='withLeftHeader doubledHeader'>
    <h1>Politechnika Biaostocka</h1>
    <article className='verticalJustified subPageContentContainer'>
      <div>Jeden z testw stworzonych przeze mnie w trakcie pracy magisterskiej na potrzeby badania wydajnoci wywietlania grafiki 3D w przegldarkach. Test polega na badaniu efektywnoci wywietlania wielu obiektw 3D w zalenosci od ich ruchu, iloci i rodzajw naoonych tekstur, poruszania kamer itp.</div>
      <img
        alt={IMG_TITLE}
        title={IMG_TITLE}
        className='image100'
        src={testWebGL}
      />
    </article>
  </section>
)

export default pb2;



