import React from 'react';

const about = () => (
  <section className='withLeftHeader'>
    <h1>Wicej o mnie</h1>
    <article className='verticalJustified withMargins'>
      <div>
        Mwi si, e dzie bez nauczenia si czego nowego to dzie stracony. W moim przypadku na sowach si nie koczy. Wiadomo, e istnieje niezliczona ilo frameworkw, bibliotek i narzdzi przydatnych do tworzenia front-endu. Wic mimo ju kilkuletniego stau na tym stanowisku tak naprawd ucz si cay czas.
      </div>
      <div>
        Na obecnym etapie wgryzam si w sposoby zaawansowanego uywania react'a i redux'a oraz bibliotek na nich opartych, np. <a href='https://github.com/ReactTraining/react-router' target='_blank' rel='noopener noreferrer'>react-router</a>. Jestem te w trakcie poszerzania wiedzy na temat sposobw dodawania elementw back-endowych do aplikacji internetowych (np. <a href='http://graphql.org/' target='_blank' rel='noopener noreferrer'>GraphQL</a>).
      </div>
      <div>
        W miar moliwoci staram si te uczszcza rwnie na meet-upy zwizane z tematyk programowania w Java Scripcie i React'cie.
      </div>
    </article>
  </section>
)

export default about;



