import React from 'react';
import LiWithSign from '../../../components/liWithSign.js';

const Shedul2 = () => (
  <section className='withLeftHeader'>
    <h1 className="smallerTitle">SURGE Ventures</h1>
    <article className='descSections subPageContentContainer'>
      <br />
      <div>
        <div>Stack front-end'u</div>
        <div>React w wersji od v16.0.0 do v16.4.0, 
          redux,
          redux-ORM,
          postcss,
          yarn,
          Git,
          GitHub,
          prettier,
          storybook,
          metodologia Scrum.
        </div>
      </div>
      <div>
        <div>moje zadania</div>
        <div>
          <ul>
            1. Team integracyjny:
            <LiWithSign>naprawa zgoszonych bugw na rnych urzdzeniach</LiWithSign>
            <LiWithSign>wppraca z testerami przy zgaszaniu wychwyconych bugw</LiWithSign>
            <br />
            2. Team scrum'owy:
            <LiWithSign>wsptworzenie nowych feature'w zaprojektowanych przez team produktowy</LiWithSign>
            <LiWithSign>rozszerzanie funkcjonalnoci istniejcych widokw i komponentw</LiWithSign>
            <LiWithSign>wsppraca z innymi teamami scrumowymi</LiWithSign>
            <LiWithSign>uczestnictwo we frontendowej midzyteamowej gildii</LiWithSign>
          </ul>
        </div>
        <br />
        <div>
          <div>
            <span>link do strony firmy na </span>
            <a
            href='https://github.com/surgeventures/'
            target='_blank'
            rel='noopener noreferrer'>
               GitHub'ie
            </a>.
          </div>
        </div>
      </div>
    </article>
  </section>
)

export default Shedul2;



