import React from 'react';
import stareMiasto from '../../images/stareMiasto.jpg';

const piano = () => (
  <section className='withLeftHeader doubledHeader'>
    <h1>nauk gry na pianinie</h1>
    <article>
      <div>
        Jestem samoukiem i bardzo chciabym posi umiejtno improwizacji na pianine. Do tego musz pozna wszystkie tonacje i akordy. Moim celem jest kiedy zagra w Studiu im. Agnieszki Osieckiej w radiowej Trjce!
      </div>
      <img className='image80 smaller' src={stareMiasto} alt='Pianino na Starym Miecie'/>
    </article>
  </section>
)

export default piano;



