import React from 'react';
import kamilograpp from '../../images/kamilograpp.PNG';

const kamilograpp1 = () => (
  <section className='withLeftHeader'>
    <h1>Mj autorski projekt</h1>
    <article>
      <div>
        "Powiedz mi, a zapomn, poka mi, a zapamitam, pozwl mi zrobi, a zrozumiem"
      </div>
      <div>
        Wychodzc z tego susznego twierdzenia Konfucjusza - od razu staram si wykorzysta w praktyce materia jaki aktualnie przyswajam. Dlatego w wolnym czasie tworz autorsk aplikacj do nauki nut na pianino. Z jednej strony chciabym j w przyszoci rozwin do poziomu komercyjnego, a z drugiej - suy mi wanie jako poligon do praktycznego rozwoju swoich umiejtnoci.
      </div>
      <div>
        <a
          href="https://kamilogram.github.io/kamilograpp/"
          target='_blank'
          rel='noopener noreferrer'
          title='Otwrz https://kamilogram.github.io/kamilograpp/'>
            <img
              className='image100'
              src={kamilograpp}
              alt='Strona aplikacji KamilogrAPP'
            />
        </a>
      </div>
    </article>
  </section>
)

export default kamilograpp1;



